.flex {
  display: flex;
}

.flexColumn {
  display: auto;
  flex-direction: column;
}

.centered {
  align-items: center;
  justify-content: center;
  text-align: center;
}

.searchContainer {
  height: 100px;
  width: 100%;
}

.searchInput {
  border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
  box-shadow: 2px 2px 2px black;
  height: 20px;
  width: 20vw;
}

.queryButton {
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  box-shadow: 2px 2px 2px black;
  height: 26px;
}

.star {
  background-image: url('./images/uncoloredStar.png');
  background-size: contain;
  height: 30px;
  margin: 5px;
  width: 30px;
}

.colorStar {
  background-image: url('./images/coloredStar.png');
  background-size: contain;
  height: 15px;
  margin: 12.5px 5px;
  transform: scale(1.9);
  width: 30px;
}
